// Configuracin del botn flotante de Discord
export const DISCORD_CONFIG = {
  // URL del servidor de Discord (reemplaza con tu URL real)
  serverUrl: 'https://discord.gg/6CFXHuKckz',
  
  // Configuracin del botn
  button: {
    // Posicin del botn
    position: {
      bottom: '20px',
      right: '20px'
    },
    
    // Tamao del botn
    size: {
      width: '60px',
      height: '60px'
    },
    
    // Colores del botn
    colors: {
      primary: '#7289da',
      secondary: '#5865f2',
      hover: '#5b6eae'
    },
    
    // Animaciones
    animations: {
      // Mostrar animacin de rebote al cargar
      showBounce: true,
      // Mostrar pulso despus de 5 segundos
      showPulse: true,
      // Duracin del pulso en segundos
      pulseDelay: 5000
    }
  },
  
  // Configuracin del modal
  modal: {
    title: 'nete a nuestra Comunidad!',
    description: 'Conctate con otros desarrolladores de novelas visuales, comparte tus proyectos, obtn ayuda y participa en eventos exclusivos de la comunidad RynEditor.',
    buttonText: 'Unirse a Discord!',
    cancelText: 'Cancelar'
  },
  
  // Configuracin del tooltip
  tooltip: {
    text: 'nete a nuestro Discord!',
    showDelay: 0,
    hideDelay: 0
  },
  
  // Configuracin de notificaciones (opcional)
  notifications: {
    // Mostrar badge de notificacin
    showBadge: false,
    // Nmero de notificaciones (si showBadge es true)
    count: 0,
    // Color del badge
    badgeColor: '#ff4757'
  },
  
  // Configuracin de visibilidad
  visibility: {
    // Mostrar solo en ciertas pginas (array de rutas)
    showOnPages: [], // Vaco = mostrar en todas las pginas
    // Ocultar en ciertas pginas
    hideOnPages: ['intro'], // Ocultar en la pantalla de introduccin
    // Mostrar solo despus de cierto tiempo (en milisegundos)
    showAfterDelay: 1000
  }
};

// Funcin para obtener la configuracin
export const getDiscordConfig = () => {
  return DISCORD_CONFIG;
};

// Funcin para actualizar la URL del servidor
export const updateDiscordServerUrl = (newUrl) => {
  DISCORD_CONFIG.serverUrl = newUrl;
};

// Funcin para verificar si el botn debe mostrarse en la pgina actual
export const shouldShowDiscordButton = (currentPage) => {
  const { visibility } = DISCORD_CONFIG;
  
  // Si hay pginas especficas donde mostrar
  if (visibility.showOnPages.length > 0) {
    return visibility.showOnPages.includes(currentPage);
  }
  
  // Si hay pginas donde ocultar
  if (visibility.hideOnPages.length > 0) {
    return !visibility.hideOnPages.includes(currentPage);
  }
  
  // Mostrar por defecto
  return true;
};
